import React from 'react'
import {
    MainContainer,
    MainWrapper,
    MainSpeakerWrapper,
    MainSpeakerImg,
    MainSpeakerInfo,
    MainSpeakerH1,
    MainSpeakerDesc,
    MainSpeakerSecWrapper,
    MainSpeakerSecH3
} from './HomeMainElements'
import ProductList from '../../ProductList/'
import speakerZX9Img from '../../../assets/home/desktop/image-speaker-zx9.png'
import speakerZX7Img from '../../../assets/home/desktop/image-speaker-zx7.jpg'
import { GlobalBtnBlack, GlobalBtnWhite } from '../../../GlobalBtn'


const HomeMain = () => {
    return (
        <MainContainer>
            <MainWrapper>
                <ProductList />
                <MainSpeakerWrapper>
                    <MainSpeakerImg  data-aos="fade-up" data-aos-duration="1000" bg={speakerZX9Img} aria-label="popular speaker product image" />
                    <MainSpeakerInfo>
                        <MainSpeakerH1>ZX9 SPEAKER</MainSpeakerH1>
                        <MainSpeakerDesc>
                            Upgrade to premium speakers that are phenomenally built 
                            to deliver truly remarkable sound.
                        </MainSpeakerDesc>
                        <GlobalBtnBlack data-text="see product" aria-label="link to ZX9 speaker product detail page"/>
                    </MainSpeakerInfo>
                </MainSpeakerWrapper>
                <MainSpeakerSecWrapper data-aos="zoom-in-out" data-duration="1000" bgSrc={speakerZX7Img} aria-label="second type speaker product image">
                    <MainSpeakerSecH3>
                        ZX7 SPEAKER
                    </MainSpeakerSecH3>
                    <GlobalBtnWhite data-text="see product" aria-label="link to ZX7 speaker product detail page"/>
                </MainSpeakerSecWrapper>
                <MainSpeakerSecWrapper data-aos="zoom-in-out" data-duration="1000" data-aos-delay="100" bgSrc={speakerZX7Img} aria-label="second type speaker product image">
                    <MainSpeakerSecH3>
                        YX1 EARPHONES
                    </MainSpeakerSecH3>
                    <GlobalBtnWhite data-text="see product" aria-label="link to YX1 earphone product detail page"/>
                </MainSpeakerSecWrapper>
            </MainWrapper>
        </MainContainer>
    )
}

export default HomeMain
